import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { NotificationRouting } from '@backbase/notifications-common-ang';

@Injectable()
export class NotificationsRouterService {
  private readonly notificationsSettingsOpenedSubject = new Subject<void>();
  /**
   * Settings opened navigation subject
   */
  readonly settingsOpenedNavigation$: Observable<void> = this.notificationsSettingsOpenedSubject.asObservable();

  private readonly notificationRoutingSubject = new Subject<NotificationRouting>();
  /**
   * Notification routing subject
   */
  readonly notificationRouting$: Observable<NotificationRouting> = this.notificationRoutingSubject.asObservable();

  /**
   * Emit notification settings navigation
   */
  emitSettingsOpenedNavigation() {
    this.notificationsSettingsOpenedSubject.next();
  }

  /**
   * Emit notification routing
   *
   * @param routing Routing subject
   */
  emitNotificationRouting(routing: NotificationRouting) {
    this.notificationRoutingSubject.next(routing);
  }
}
